<template>
    <div class="container">
        <section class="row">
            <div class="col-xs-12">
                <v-breadnav>资讯</v-breadnav>
            </div>
        </section>
        <section class="row">
            <div class="col-xs-12">                
                <ul class="information-list" v-if="informationList.length != 0">
                    <div class="title"><h3>最新资讯</h3></div>
                    <li v-for="(item,index) in informationList" :key="index" class="information-item clearfix">
                        <div class="col-xs-4">
                            <div class="car-img">
                                <img :src="item.icon" alt="" class="img-responsive">
                            </div>                           
                        </div>
                        <section class="col-xs-8">
                            <h3 class="information-title">{{item.title}}</h3>
                            <p>{{`${item.shortTitle}...`}}
                                <a @click="getcarDetail(item.id,item.url)">阅读全文>></a>
                            </p>
                            <div class="item-footer">
                                <span>{{item.date}}</span>
                                <span class="pull-right information-type">{{item.type}}</span>
                            </div>
                        </section>
                    </li>
                </ul>
            </div>
            <v-pageNation :totalcount = "totalcount" @getPage = "getPage"></v-pageNation>
        </section>
    </div>
    
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vPageNation from "@/components/pc/pageNation"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            informationList:[],
            totalcount:0
        }
    },
    components:{
        vBreadnav,
        vPageNation
    },
    methods:{
        getPage:function(data){        
            this.getListData(data);
        },
        getListData:function(pageNum){
            let _this = this;
            let data = {};
            if(pageNum){
                data.pageNum = pageNum;
            }else{
                data.pageNum = 1;
            }
            data.pageSize = 10;
            data.platForm = "020003";
             $.ajax({
                url:PC_COMMONURL+'/queryNewsList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.informationList = data.data.newList;
                        _this.totalcount = data.data.count;
                    }
                }
            })
        },
        getcarDetail(id,url) {
            this.$router.push({
                name: 'informationDesc',
                params: {
                id: id,
                url:url
                }
            })
        }
    },
    mounted:function(){
        this.getListData();
    }   
}
</script>



